.swiper-container {
    width: 255px;
    height: 75px;
    position: relative;

    @media (min-width: $tablet-width) {
      width: 649px;
      height: 145px;
    }

    @media (min-width: $desktop-width) {
      width: 561px;
      height: 123px;
    }
}

.swiper-container__next-btn {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    position: absolute;
    top: calc(50% - 15px);
    z-index: 99;
    right: 0;
    width: 46px;
    height: 46px;

    &:hover {
      cursor: pointer;
      opacity: 1;
    }

    & img {
    width: 46px;
    height: 46px;
    }
  }
}

.swiper-container__prev-btn {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    position: absolute;
    z-index: 99;
    left: 0;
    transform: rotate(180deg);
    top: calc(50% - 23px);
    opacity: 0.7;


    &:hover {
      cursor: pointer;
      opacity: 1;
    }

    & img {
      width: 46px;
      height: 46px;
    }
  }
}
