.controls {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;

  margin: 20px auto 0;

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.controls__label {
	cursor: pointer;
	display: block;
  height: 3px;
  width: 60px;
	margin: 0 12px 0 0;
	position: relative;
  

  &:last-child {
    margin-right: 0;
  }

  @media (min-width: $tablet-width) {
    &--2,
    &--3 {
      display: none;
    }
  }
}

.controls__label::after {
  content: "";
  position: absolute;
  height: 3px;
  width: 100%;
  background: rgba(255, 255, 255, 0.3);
}

.controls__label:hover {
  background-color: $white;
}

@media (max-width: $mobile-width-only) {
  #slide1:checked ~ .catalog__list > .services-card--1,
  #slide2:checked ~ .catalog__list > .services-card--2,
  #slide3:checked ~ .catalog__list > .services-card--3,
  #slide4:checked ~ .catalog__list > .services-card--4 {
    opacity: 1;
    z-index: 1;
  }
}

@media (min-width: $tablet-width) {
  #slide1:checked ~ .catalog__list > .services-card--1 {
    opacity: 1;
    z-index: 1;
  }

  #slide1:checked ~ .catalog__list > .services-card--2 {
    opacity: 1;
    z-index: 1;
  }

  #slide4:checked ~ .catalog__list > .services-card--3 {
    opacity: 1;
    z-index: 1;
  }

  #slide4:checked ~ .catalog__list > .services-card--4 {
    opacity: 1;
    z-index: 1;
  }
}

#slide1:checked ~ .controls label[for=slide1],
#slide2:checked ~ .controls label[for=slide2],
#slide3:checked ~ .controls label[for=slide3],
#slide4:checked ~ .controls label[for=slide4] {
  background-color: $white;
}
