.header {
  @media (min-width: $desktop-width) {
    position: relative;
  }
}

.header--opened {
  @media (min-width: $desktop-width) {
    background-color: $bg-color;
  }
}


.header__logo-image {
  margin-left: 30px;
  margin-top: 15px;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    width: 137px;
    height: 47px;
    margin-top: 38px;
    margin-left: 50px;
    margin-bottom: 35px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 87px;
    margin-left: 96px;
    margin-bottom: 0;
  }
}

.header__logo--opened {
  margin-left: calc(50% - 48px);
  margin-top: 26px;

  @media (min-width: $tablet-width) {
    margin-left: calc(50% - 68px);
  }

  & .header__logo-image {
    margin-top: 25px;
    margin-left: 0;

    @media (min-width: $tablet-width) {
      margin-top: 43px;
    }
  }
}

.header__social {
  display: none;
}

.header__social--opened {
  @media (min-width: $desktop-width) {
    display: block;
    position: absolute;
    bottom: 95px;
    left: 95px;
  }
}

