.slider {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;

  @media (min-width: $desktop-width) {
    justify-content: flex-start;
    margin-left: 114px;
  }
}

.slider__img {
  width: 75px;
  height: 75px;
  margin-right: 15px;

  @media (min-width: $tablet-width) {
    width: 145px;
    height: 145px;
    margin-right: 23px;
  }

  @media (min-width: $desktop-width) {
    width: 123px;
    height: 123px;
  }
};

