.promo-block {
  background-image: url("../img/bg-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;

  @media (min-width: $tablet-width) {
    background-image: none;
    position: relative;

    &::before {
      content: "";
      background-image: url("../img/bg-tablet@1x.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      width: 100%;
      height: 1024px;
      position: absolute;
      z-index: -1;
      top: -128px;

      @media (min-width: $desktop-width) {
        background-image: url("../img/bg-desktop@1x.jpg");
        height: 1044px;
        top: -162px;
      }
    }

  }

  @media (min-width: $desktop-width) {
    background: linear-gradient(0deg, #3B181E 3.33%, rgba(59, 24, 30, 0) 100%);
    padding-bottom: 327px;
  }

}

.promo-block__title {
  margin: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 30px;
  line-height: 140%;
  text-transform: uppercase;
  padding: 103px 75px 35px;

  text-align: center;
  letter-spacing: 0.09em;

  @media (min-width: $tablet-width) {
    font-size: 70px;
    line-height: 77px;
    letter-spacing: 0.09em;
    padding: 246px 100px 82px;
  }

  @media (min-width: $desktop-width) {
    font-size: 120px;
    line-height: 132px;
    width: 818px;
    margin: 0 auto;
    padding: 189px 0 80px;
  }
}

.promo-block__link {
  width: 177px;
  height: 50px;
  font-size: 10px;
  line-height: 11px;
  text-align: center;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  text-decoration: none;
  color: $white;

  margin: 0 auto;
  padding-top: 19px;

  display: block;

  background: rgba(0, 0, 0, 0.4);
  border: 1px solid #FFFFFF;
  box-sizing: border-box;

  &:hover {
    color: $black;
    background-color: $white;
  }

  @media (min-width: $tablet-width) {
    width: 257px;
    height: 60px;
    font-size: 15px;
    line-height: 17px;
    padding-top: 21px;
  }

  @media (min-width: $desktop-width) {
    width: 199px;
    height: 50px;
    font-size: 12px;
    line-height: 13px;
    padding-top: 19px;
  }
}

.promo-block__social {
  padding-top: 70px;
  padding-bottom: 89px;

  @media (min-width: $tablet-width) {
    padding-top: 211px;
    padding-bottom: 35px;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    z-index: 11;
    padding: 0;
    left: 95px;
    bottom: 96px;
  }
}

.promo-block__down-link {
  @media (min-width: $desktop-width) {
    position: absolute;
    right: 15px;
    bottom: 210px;
    transform: rotate(90deg);
  }
}

.promo-block__arrow-icon {
  display: block;
  margin: 0 auto;
  padding-bottom: 62px;

  @media (min-width: $desktop-width) {
    padding-bottom: 0;
    width: 13.42px;
    height: 13.42px;

    position: absolute;
    margin: 0;
    bottom: 3px;
    transform: rotate(-90deg);
    right: -25px;
  }
}

.promo-block__down-text {
  display: none;

  @media (min-width: $desktop-width) {

    display: inline;
    font-weight: normal;
    font-size: 20px;
    line-height: 22px;
    color: $white;
    text-decoration: none;

    letter-spacing: 0.06em;
    position: relative;

  }
}
