.popup {
  position: fixed;
  width: 288px;
  height: 426px;
  box-sizing: border-box;
  background-color: $bg-color;
  z-index: 99;
  
  left: calc(50% - 144px);
  bottom: calc(50% - 150px);

  padding: 62px 20px 52px;

  display: none;
  box-shadow: 0px 0px 33px -3px rgba(0,0,0,0.75);

  @media (min-width: $tablet-width) {
    width: 464px;
    height: 640px;

    padding: 104px 44px 78px;
    left: calc(50% - 232px);
    bottom: 150px;
  }

  @media (min-width: $desktop-width) {
    width: 716px;
    height: 444px;

    flex-direction: row;
    flex-wrap: nowrap;
    padding: 0;
    left: calc(50% - 368px);
    top: calc(50% - 220px);
  }
}

.popup__form {
  @media (min-width: $desktop-width) {
    width: 381px;
    padding: 66px;
    box-sizing: border-box;
  }
}

.popup__img {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    width: 335px;
    background-image: url("../img/bg-form@1x.jpg");
  }
}

.popup--opened {
  display: block;
  z-index: 99;

  @media (min-width: $desktop-width) {
    display: flex;
  }
}

.popup__toggle {
  position: absolute;
  top: 20px;
  right: 20px;

  display: block;
  width: 22px;
  height: 22px;
  background-color: $bg-color;
  cursor: pointer;
  border: none;
}

.popup__toggle {
  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 8px;
    right: 0;
    width: 22px;
    height: 2px;
    background-color: rgba(255, 255, 255, 0.3);
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.popup__title {
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 45px;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 33px;
    letter-spacing: 0.09em;
    padding-bottom: 75px;
  }

  @media (min-width: $desktop-width) {
    font-size: 20px;
    line-height: 22px;
    padding-bottom: 45px;
    padding-left: 25px;
  }
}

.popup__fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

.popup__input {
  font-size: 14px;
  line-height: 15px;
  letter-spacing: -0.01em;
  padding: 14px 0 10px 20px;
  color: $white;
  background-color: transparent;
  border: 1px solid rgba(246, 246, 249, 0.2);
  margin-bottom: 16px;

  width: 100%;
  box-sizing: border-box;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 22px;
    padding: 20px 33px;
    margin-bottom: 25px;
  }

  @media (min-width: $desktop-width) {
    padding: 14px 0 10px 20px;
    margin-bottom: 16px;
  }
}

.popup__btn {
  width: 100%;
  font-size: 11px;
  line-height: 12px;
  text-align: center;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: $black;
  padding: 14px 0;
  margin-top: 15px;

  @media (min-width: $tablet-width) {
    padding: 25px 0;
    font-size: 15px;
    line-height: 17px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 15px;
    font-size: 11px;
    line-height: 12px;
    padding: 16px 0;
  }

}
