.footer {
  margin: 0 15px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin: 0 21px;
    padding-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 52px;
  }
}

.footer__site-nav {
  padding-top: 30px;

  @media (min-width: $desktop-width) {
    padding-top: 21px;
  }
}

.footer__social {
  & .social__list {
    width: 250px;
  }
}
