.offer {
  padding: 80px 16px 100px;
  position: relative;

  @media (min-width: $tablet-width) {
    text-align: center;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 150px 0 170px;
  }
}

.offer__img-wrapper {
  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    grid-row: 1/3;
  }
}

.offer__text-wrapper {
  @media (min-width: $desktop-width) {
    grid-column: 1/2;
    grid-row: 1/3;

    text-align: left;
    padding-left: 200px;
  }

  @media (min-width: 1920px) {
    padding-left: 373px;
  }
}

.offer__image {
  width: 100%;

  @media (min-width: $tablet-width) {
    width: 453px;
    height: 323px;

    display: block;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    margin-left: 0;
    width: 600px;
    height: auto
  }

  @media (min-width: 1920px) {
    width: 697px;
  }
}

.offer__title {
  font-size: 20px;
  line-height: 170%;
  /* or 34px */

  letter-spacing: 0.06em;
  text-transform: uppercase;

  margin: 0;
  padding: 25px 80px 20px 0;

  position: relative;

  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 170%;
    letter-spacing: 0.06em;

    padding: 25px 0 15px;
  }

  @media (min-width: $desktop-width) {
    padding-right: 100px;
    padding-top: 65px;
  }
}

.offer__title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;

  width: 85.5px;
  height: 2px;
  background-color: $white;

  @media (min-width: $tablet-width) {
    left: calc(50% - 42.5px);
  }

  @media (min-width: $desktop-width) {
    left: 0;
  }
}

.offer__text {
  font-size: 14px;
  line-height: 170%;
  padding-bottom: 40px;

  margin: 0;
  padding-top: 20px;

  @media (min-width: $tablet-width) {
    padding: 40px 140px 15px;
  }

  @media (min-width: $desktop-width) {
    padding: 25px 160px 45px 0;
  }
}

.offer__button {
  width: 220px;
  height: 50px;
  background-color: $black;
  border: none;

  font-size: 11px;
  line-height: 12px;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: $white;

  @media (min-width: $tablet-width) {
    height: 45px;
  }
}
