.social__list {
  @include list-reset;

  display: flex;
  flex-direction: row;
  margin: 0 auto;
  width: 260px;
  justify-content: space-between;

  @media (min-width: $desktop-width) {
    flex-direction: column;
    width: auto;
    height: 171.67px;
  }
}


.social__item {
  background-color: $bg-color;
  width: 35px;
  height: 35px;
  border-radius: 25px;

  @media (min-width: $tablet-width) {
    width: 55px;
    height: 55px;
  }

  @media (min-width: $desktop-width) {
    background-color: transparent;
  }

}

.social__icon {
  padding-top: 9px;
  padding-left: 9px;

  @media (min-width: $tablet-width) {
    width: 29px;
    height: 29px;
    padding-top: 13px;
    padding-left: 14px;
  }

  @media (min-width: $desktop-width) {
    width: 25px;
    height: 25px;
  }
}


.social__item--footer {
  width: 25px;
  height: 25px;

  & .social__icon {
    width: 25px;
    height: 25px;
    padding: 0;
  }
}

.social__list--footer {
  @media (min-width: $desktop-width) {
    height: auto;
    flex-direction: row;
  }
}
