.catalog {
  padding: 100px 0 20px;

  position: relative;

  & >input {
    display: none;
  }
}

.catalog__list {
  min-height: 412px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    min-height: 480px;
  }

  @media (min-width: $tablet-width) {
    display: flex;
    width: 100%;
    justify-content: center;
  }
}

.catalog__item {
  height: 392px;
  width: 288px;
  position: absolute;
  margin-left: calc(50% - 144px);
  z-index: 0;
  opacity: 0;
  transition: transform ease-in-out .5s, opacity ease-in-out .5s;

  @media (min-width: $tablet-width) {
    position: absolute;
    z-index: 0;
    opacity: 0;
    margin: 0;

    width: 330px;
    height: 450px;

    &--1,
    &--3 {
      left: 40px;

      @media (min-width: 900px) {
        left: 10%;
      }

      @media (min-width: 1000px) {
        left: 15%;
      }
    };

    &--2,
    &--4 {
      right: 40px;

      @media (min-width: 900px) {
        right: 10%;
      }

      @media (min-width: 1000px) {
        right: 15%;
      }
    }
  }

  @media (min-width: $desktop-width) {
    position: static;
    z-index: 1;
    opacity: 1;
    width: calc(23% - 60px);
    margin-right: 60px;

    &:last-child {
      margin-right: 0;
    }

    @media (min-width: 1500px) {
      width: calc(20% - 60px);
    }
  }
}

.catalog__title {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 170%;
  letter-spacing: 0.06em;
  text-transform: uppercase;

  margin: 0;

  padding-bottom: 20px;
  text-align: center;
  padding-right: 95px;

  @media (min-width: $tablet-width) {
    padding: 0;
    padding-bottom: 52px;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 85.5px;
      height: 2px;
      background-color: $white;
      bottom: 30px;
      right: calc(50% - 42px);

      @media (min-width: $desktop-width) {
        display: none;
      }

    }
  }

  @media (min-width: $desktop-width) {
    font-size: 30px;
    line-height: 33px;
    letter-spacing: 0.06em;
    padding-bottom: 120px;
    position: relative;
  }
}

@media (min-width: $desktop-width) {
  .catalog__title::before {
    content: "КАТАЛОГ УСЛУГ";
    position: absolute;
    left: calc(50% - 569px);
    bottom: 0;
    color: white;
    opacity: 0.03;
    font-size: 150px;
    line-height: 243px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
  }
}
