.service {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 947px;
    position: relative;
  }
}

.service__img {
  height: 443px;
  width: 100%;
  background-image: url("../img/bg-services-1-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  &--composition {
    background-image: url("../img/bg-services-3-mobile@1x.jpg");
  }

  &--event {
    background-image: url("../img/bg-services-4-mobile@1x.jpg");
  }

  &--wedding {
    background-image: url("../img/bg-services-2-mobile@1x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/bg-services-1-desktop@1x.jpg");
    height: 518px;

    &--composition {
      background-image: url("../img/bg-services-3-desktop@1x.jpg");
    }

    &--event {
      background-image: url("../img/bg-services-4-desktop@1x.jpg");
    }

    &--wedding {
    background-image: url("../img/bg-services-2-desktop@1x.jpg");
  }
  }

  @media (min-width: $desktop-width) {
    width: 50%;
    position: absolute;
    bottom: 0;
    height: 1086px;
    z-index: -1;
  }
}

.service__description {
  padding: 30px 0 45px;
  position: relative;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    padding-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    padding-top: 119px;
  }
}

.service__title {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  padding-right: 100px;
  margin: 0;
  padding-left: 20px;

  @media (min-width: $tablet-width) {
    order: 2;
    font-size: 40px;
    line-height: 44px;
    padding: 0 50px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 114px;
    font-size: 50px;
    line-height: 55px;
  }
}

.service__text {
  font-size: 18px;
  line-height: 120%;
  margin: 0;
  padding: 30px 20px;

  @media (min-width: $tablet-width) {
    order: 3;
    padding: 30px 50px;
    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 114px;
    padding-right: 15%;
    font-size: 18px;
    line-height: 150%;
  }
}

.service__button {
  position: relative;

  border: 1px solid #FFFFFF;
  box-sizing: border-box;

  width: 119px;
  height: 45px;
  margin-left: 20px;

  background-color: transparent;

  font-size: 10px;
  color: $white;
  line-height: 11px;
  text-align: center;
  letter-spacing: 0.15em;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    order: 4;
    margin-left: 50px;

    width: 169px;
    height: 60px;

    font-size: 15px;
    line-height: 17px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 114px;

    width: 135px;
    height: 50px;

    font-size: 12px;
    line-height: 13px;
  }
}

.service__slider {
  overflow: hidden;
  margin-top: 50px;

  @media (min-width: $tablet-width) {
    order: 1;
    margin-top: 0;
    margin-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    order: 4;
    margin-top: 100px;
  }
}
