.services-card {
  padding-top: 23px;
  padding-bottom: 15px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-repeat: no-repeat;
  background-size: cover;

  @media (min-width: $tablet-width) {
    padding-top: 42px;
  }
}

.services-card--1 {
  background-image: url("../img/services-1@1x.jpg");

  @media (min-width: $desktop-width) {
    justify-content: flex-end;
    & .services-card__link {
      padding-bottom: 20px;
    }

    & .services-card__counter {
      margin-bottom: auto;
    }
  }
}

.services-card--2 {
  background-image: url("../img/services-2@1x.jpg");
}

.services-card--3 {
  background-image: url("../img/services-3@1x.jpg");
}

.services-card--4 {
  background-image: url("../img/services-4@1x.jpg");
}

.services-card__counter {
  font-size: 20px;
  line-height: 22px;
  text-transform: uppercase;

  margin: 0;
  padding-left: 100px;

  position: relative;

  @media (min-width: $tablet-width) {
    padding-left: 20px;
  }
}

.services-card__counter::before {
  content: "";
  position: absolute;
  width: 70px;
  height: 1px;
  background-color: $white;
  left: 20px;
  top: 10px;

  @media (min-width: $tablet-width) {
    left: 50px;
  }
}

.services-card__link {
  font-size: 16px;
  line-height: 170%;

  letter-spacing: 0.1em;
  text-transform: uppercase;

  text-decoration: none;
  color: $white;

  opacity: 0.9;

  padding: 280px 20px 0;
  margin: 0;

  display: block;
  height: 100%;

  @media (min-width: $tablet-width) {
    padding-top: 330px;
  }
}

.services-card__link--button {
  display: none;

    @media (min-width: $desktop-width) {
      display: block;
      color: $black;
      width: 199px;
      height: 50px;
      box-sizing: border-box;
      margin-left: 20px;
      padding-top: 19px;
      background-color: $white;

      font-size: 12px;
      line-height: 13px;
      text-align: center;
      letter-spacing: 0.15em;
  }
}

