.site-nav {
  position: relative;
  background-color: $bg-color;
}

.site-nav__list {
  @include list-reset;
  padding-top: 54px;

  @media (min-width: $desktop-width) {
    position: relative;
  }
}

.site-nav__item {
  text-align: center;
  text-transform: uppercase;
  padding: 24px 88px;
  font-weight: normal;
  font-size: 15px;
  line-height: 17px;
  letter-spacing: 0.09em;

  @media (min-width: $tablet-width) {
    padding: 32px 0;
    width: 300px;
    margin: 0 auto;

    font-size: 30px;
    line-height: 33px;

    &:last-child {
      padding-bottom: 161px;
    }
  }
}

.site-nav__item--title {
  font-weight: normal;
  font-size: 25px;
  line-height: 28px;
  letter-spacing: 0.09em;
  padding-top: 0;
  padding-bottom: 31px;
  width: 171px;
  margin: 0 auto;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: 45px;
    line-height: 50px;
    width: 307px;
    padding-bottom: 38px;
  }

}

.site-nav__link {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
  position: relative;

  &:hover {
    color: $white;
  }
}

.site-nav__toggle {
  position: absolute;
  top: -50px;
  right: 0;

  margin-right: 30px;

  display: block;
  width: 37px;
  height: 21px;
  background-color: $bg-color;
  cursor: pointer;
  border: none;

  outline: none;

  @media (min-width: $tablet-width) {
    background-color: transparent;
    top: -70px;
  }

  @media (min-width: $desktop-width) {
    top: -50px;
  }
}

.site-nav--closed .site-nav__toggle::before {
  content: "";

  position: absolute;
  top: 5px;
  left: 0;

  width: 37px;
  height: 1px;

  background-color: $white;
  box-shadow: 0 10px 0 0 $white;

  @media (min-width: $desktop-width) {
    width: 23px;
    left: -35px;
  }
}

@media (min-width: $desktop-width) {
  .site-nav--closed .site-nav__toggle {
    right: 96px;
  }

  .site-nav--closed .site-nav__toggle::after {
    content: "МЕНЮ";
    color: $white;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.06em;

    position: absolute;
    left: 0;
    top: 0;
  }
}

.site-nav--closed .site-nav__toggle:active::before {
  background-color: $white;
  box-shadow: 0 10px 0 0 $white;
}

.site-nav--opened .site-nav__toggle {
  width: 21px;
  height: 21px;

  @media (min-width: $tablet-width) {
    top: -90px;
  }

  @media (min-width: $desktop-width) {
    width: 28px;
    height: 28px;
    top: -59px;
    right: 69px;
  }

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 11px;
    right: 0px;
    width: 21px;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.5);

    @media (min-width: $desktop-width) {
      width: 28px;
    }
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: $white;
  }
}

.site-nav--closed .site-nav__list {
  display: none;
}


.site-nav--nojs .site-nav__toggle {
  display: none;
}

.site-nav__list--footer {
  padding: 0;
  padding-bottom: 30px;

  @media (min-width: $tablet-width) {
    padding-bottom: 40px;

    & .site-nav__item:last-child {
      padding-bottom: 0;
    }
  }

  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0 auto;
    padding-bottom: 30px;
  }
}

.site-nav__item--footer {
  padding: 0 0 15px;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;

  color: rgba(255, 255, 255, 0.5);
  text-transform: none;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 22px;
  }

  @media (min-width: $desktop-width) {
    font-size: 16px;
    line-height: 18px;
    width: auto;
    margin: 0;
    margin-right: 50px;

    &:last-child {
      margin-right: 0;
    }
  }
}

